export var HYDRATE_DEAL = 'app/HYDRATE_DEAL';
export var CLEAR_DEAL = 'app/CLEAR_DEAL';
export var hydrateDeal = function (deal) {
    return ({
        type: HYDRATE_DEAL,
        payload: { deal: deal },
    });
};
export var clearDeal = function () {
    return ({
        type: CLEAR_DEAL,
    });
};
