import { createSlice } from '@reduxjs/toolkit';
import { getErrorMessage } from '../../../helpers/errors';
import { fetchDealStatus, saveDealStatus, saveSettings } from './async-thunks';
var initialState = {
    dealStatus: null,
    dealError: null,
    settingsError: null,
    isLoading: false,
    isSavingDealStatus: false,
    isSavingSettings: false,
    isDealStatusSaved: false,
    isSettingsSaved: false,
    settingsRequestId: performance.now(),
};
var settingsSlice = createSlice({
    name: 'settings',
    initialState: initialState,
    reducers: {},
    extraReducers: function (builder) {
        builder.addCase(fetchDealStatus.fulfilled, function (state, action) {
            state.dealStatus = action.payload;
            state.dealError = null;
        });
        builder.addCase(fetchDealStatus.rejected, function (state, action) {
            state.dealError = getErrorMessage(action.payload).message;
        });
        builder.addCase(saveSettings.fulfilled, function (state) {
            state.isSavingSettings = false;
            state.settingsError = null;
            state.isSettingsSaved = true;
            state.settingsRequestId = performance.now();
        });
        builder.addCase(saveSettings.rejected, function (state, action) {
            state.isSavingSettings = false;
            state.isSettingsSaved = false;
            state.settingsRequestId = performance.now();
            state.settingsError = getErrorMessage(action.payload).message;
        });
        builder.addCase(saveDealStatus.fulfilled, function (state, action) {
            state.isSavingDealStatus = false;
            state.isDealStatusSaved = true;
            state.dealError = null;
            state.dealStatus = action.payload;
        });
        builder.addCase(saveDealStatus.rejected, function (state, action) {
            state.isSavingDealStatus = false;
            state.isDealStatusSaved = false;
            state.dealError = getErrorMessage(action.payload).message;
        });
        builder.addCase(saveDealStatus.pending, function (state) {
            state.isSavingDealStatus = true;
            state.dealError = null;
            state.isDealStatusSaved = false;
        });
        builder.addCase(saveSettings.pending, function (state) {
            state.isSavingSettings = true;
            state.isSettingsSaved = false;
            state.settingsError = null;
        });
    },
});
export default settingsSlice.reducer;
